import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { RxStringService, RxUrlUtilsService } from '@helix/platform/utils';
import { RecordsSource } from './view-action.types';
import { IRowDataItem, IRxRecordGridApi, RowDataItemIdFieldName } from '../common/record-grid.types';
import { clone, compact, flow, isFunction, isObject, map, some, uniq } from 'lodash';
import { IPlainObject, RxBundleCacheService } from '@helix/platform/shared/api';
import { ViewDisplayType } from '../domain/view-display-type.enum';
import { IViewInputParams } from '../domain/view-input-params.interface';

@Injectable({
  providedIn: 'root'
})
export class RxViewActionUtilsService {
  constructor(
    private rxBundleCacheService: RxBundleCacheService,
    private router: Router,
    private rxStringService: RxStringService,
    private rxUrlUtilsService: RxUrlUtilsService
  ) {}

  generateViewUrl(viewDefinitionName: string, inputParams: IViewInputParams = {}): string {
    const currentUrl = this.router.routerState.snapshot.url;

    let displayType = ViewDisplayType.Regular;

    if (currentUrl.includes(`/${ViewDisplayType.Preview}/`)) {
      displayType = ViewDisplayType.Preview;
    } else if (currentUrl.includes(`/${ViewDisplayType.NoShell}/`)) {
      displayType = ViewDisplayType.NoShell;
    }

    return this.rxUrlUtilsService.buildUrl(
      `/${this.rxBundleCacheService.bundleId}/${displayType}/${viewDefinitionName}`,
      inputParams
    );
  }

  extractRecordIds(source: RecordsSource): string[] {
    let ids = [];

    if (isFunction((<IRxRecordGridApi>source).getSelectedRows)) {
      ids = this.getIdsFromGridRows((<IRxRecordGridApi>source).getSelectedRows());
    } else if (isObject(source) && this.rxStringService.isNonEmptyString(source[RowDataItemIdFieldName])) {
      ids.push(source[RowDataItemIdFieldName]);
    } else if (Array.isArray(source) && some(source, isObject)) {
      ids = this.getIdsFromGridRows(<IRowDataItem[]>source);
    } else if (this.rxStringService.isNonEmptyString(source)) {
      ids.push(source);
    } else if (Array.isArray(source) && some(source, this.rxStringService.isNonEmptyString)) {
      ids = clone(source);
    }

    return ids;
  }

  getIdsFromGridRows(rows: IPlainObject[]): string[] {
    return flow((rowsArray) => map(rowsArray, RowDataItemIdFieldName), compact, uniq)(rows);
  }
}
