import { Injectable } from '@angular/core';
import { get, isNil } from 'lodash';
import { RxJsonParserService } from '@helix/platform/utils';
import { IOldViewLayout, IOldViewLayoutColumn, IOldViewLayoutColumnChild } from './old-view-layout.types';
import {
  AnyViewComponentDefinition,
  IContainerViewComponentDefinition
} from '../domain/view-component-definition.types';
import { IViewComponentDefinitionWithParent } from '../domain/view-component-definition-with-parent.interface';
import { IViewDefinition } from '../domain/view-definition.interface';
import { RX_VIEW_DEFINITION } from '../domain/view-definition.constant';
import { IViewLayout, IViewLayoutOutlet, IViewLayoutOutletColumn, ViewLayoutRole } from './view-layout.types';

@Injectable({
  providedIn: 'root'
})
export class RxOldViewLayoutAdapterService {
  constructor(private rxJsonParserService: RxJsonParserService) {}

  convertLayout(componentDefinitionItem: IViewComponentDefinitionWithParent): void {
    if (this.isViewDefinitionItem(componentDefinitionItem)) {
      this.convertViewLayout(componentDefinitionItem.componentDefinition as any);
    } else if (this.isContainerComponentDefinition(componentDefinitionItem.componentDefinition)) {
      this.convertComponentLayout(componentDefinitionItem.componentDefinition);
    }
  }

  private isViewDefinitionItem(componentDefinition: IViewComponentDefinitionWithParent): boolean {
    return !componentDefinition.parentComponentDefinition;
  }

  private convertViewLayout(viewDefinition: IViewDefinition): void {
    const layout: IOldViewLayout = this.rxJsonParserService.tryParseJson(viewDefinition.layout);

    if (isNil(layout.layoutTemplate) || get(layout, 'columns[0].role')) {
      // for views from new designer, or for views with 1 or more columns
      this.convertComponentLayout(viewDefinition);
    } else {
      // for views with header and/or footer
      const childrenWithRoles: IOldViewLayoutColumnChild[] = get(layout, 'columns[0].children', []);
      const outlets = childrenWithRoles.map((child, i) => {
        let outletItem: IViewLayoutOutlet;

        if (child.role) {
          outletItem = {
            name: child.role === ViewLayoutRole.Content ? RX_VIEW_DEFINITION.defaultOutletName : child.role,
            static: child.static || null,
            height: child.height || null,
            role: child.role || null,
            columns: (child.columns || []).map(this.convertColumns.bind(this))
          };
        } else {
          // creating outlets for case when components are sibling to header/footer
          outletItem = {
            name: `outlet-${i}`,
            columns: [{ children: [child.componentDefinitionId] }]
          };
        }

        return outletItem;
      });

      const newLayout: IViewLayout = { outlets };

      viewDefinition.layout = JSON.stringify(newLayout);
    }
  }

  private convertComponentLayout(componentDefinition: IContainerViewComponentDefinition | IViewDefinition): void {
    if (componentDefinition.layout) {
      const layout: IOldViewLayout = this.rxJsonParserService.tryParseJson(componentDefinition.layout);

      // add columns to views created in new designer
      // todo remove after new designer will save layouts with columns
      const children = get(layout, 'outlets[0].children');

      if (children) {
        componentDefinition.layout = JSON.stringify(this.convertNewDesignerLayout(layout));
      }

      if (layout && layout.componentDefinitionId) {
        const newLayout = {
          outlets: [
            {
              name: RX_VIEW_DEFINITION.defaultOutletName,
              columns: layout.columns.map(this.convertColumns.bind(this))
            }
          ]
        };

        componentDefinition.layout = JSON.stringify(newLayout);
      }
    }
  }

  private convertNewDesignerLayout(layout): IViewLayout {
    const children = get(layout, 'outlets[0].children');

    if (children) {
      layout.outlets[0].columns = [
        {
          children
        }
      ];

      delete layout.outlets[0].children;
    }

    return layout;
  }

  private convertColumns(column: IOldViewLayoutColumn): IViewLayoutOutletColumn {
    // rx-tab-container was placed 1 lever deeper in the layout of rx-tab-panel in views
    // created in AngularJS view designer. 'columns[0].children[0]' should extract it.
    return {
      span: column.span || null,
      cssClass: `col-sm-${column.span || 12}`,
      children: column.children.map(
        (child) => get(child, 'columns[0].children[0].componentDefinitionId') || child.componentDefinitionId
      )
    };
  }

  private isContainerComponentDefinition(
    componentDefinition: AnyViewComponentDefinition
  ): componentDefinition is IContainerViewComponentDefinition {
    return (componentDefinition as IContainerViewComponentDefinition).componentDefinitions !== undefined;
  }
}
