import { Injectable } from '@angular/core';
import { Resolve } from '@angular/router';
import { Observable } from 'rxjs';
import { RxViewComponentRegistryService } from '../registries/view-component-registry.service';

@Injectable({
  providedIn: 'root'
})
export class RxViewComponentResolver implements Resolve<any> {
  constructor(private rxViewComponentRegistryService: RxViewComponentRegistryService) {}

  resolve(): Observable<any> {
    return this.rxViewComponentRegistryService.resolveAsyncDescriptors();
  }
}
