import { RxDefaultExpressionEvaluatorService } from './default-expression-evaluator.service';
import { RxExpressionHelperService } from './expression-helper.service';
import { IExpressionValidator } from './expression-validator.interface';
import { Injectable } from '@angular/core';
import { RxStringService } from '@helix/platform/utils';
import { IExpressionEvaluator } from './expression-evaluator.types';
import { isFunction } from 'lodash';

@Injectable({
  providedIn: 'root'
})
export class RxDefaultExpressionValidatorService implements IExpressionValidator {
  constructor(
    private rxStringService: RxStringService,
    private rxDefaultExpressionEvaluatorService: RxDefaultExpressionEvaluatorService,
    private rxExpressionHelperService: RxExpressionHelperService
  ) {}

  isValid(expression: string, customEvaluatorService?: IExpressionEvaluator): boolean {
    const evaluator = customEvaluatorService || this.rxDefaultExpressionEvaluatorService;
    let valid = true;

    if (this.rxStringService.isNonEmptyString(expression) && isFunction(evaluator.parseExpression)) {
      const preparedExpression = this.rxExpressionHelperService.prepare(expression);

      try {
        evaluator.parseExpression(preparedExpression);
      } catch (e) {
        valid = false;
      }
    }

    return valid;
  }
}
