import { RX_VIEW_DEFINITION } from '../domain/view-definition.constant';
import { reduce } from 'lodash';
import { getLayoutChecksum, IViewLayout, IViewLayoutOutlet, RX_VIEW_LAYOUTS } from './view-layout.types';

// @dynamic
export class RxViewLayout {
  static getViewLayoutForDefaultOutlet(children?: string[]): IViewLayout {
    return {
      outlets: [this.getOutlet(RX_VIEW_DEFINITION.defaultOutletName, children)]
    };
  }

  static getEmptyViewLayoutForOutletNames(list: { name: string }[]): IViewLayout {
    return {
      outlets: list.map(({ name }) => this.getOutlet(name))
    };
  }

  static getEmptyViewLayoutForOutlets(outlets: IViewLayoutOutlet[]): IViewLayout {
    return {
      outlets: outlets.map((outlet) => {
        return {
          ...outlet,
          columns: outlet.columns.map((column) => ({ ...column, children: [] }))
        };
      })
    };
  }

  static getOutlet(name = RX_VIEW_DEFINITION.defaultOutletName, children: string[] = []): IViewLayoutOutlet {
    return {
      name,
      columns: [
        {
          children
        }
      ]
    };
  }

  static getViewLayoutChildGuids(layout: IViewLayout, outletName?: string): string[] {
    return reduce(
      layout.outlets,
      (guids, outlet) => {
        if (!outletName || outletName === outlet.name) {
          outlet.columns.forEach((column) => {
            guids.push(...column.children);
          });
        }

        return guids;
      },
      []
    );
  }

  static hasChild(layout: IViewLayout, guid: string): boolean {
    return Boolean(layout.outlets.find((outlet) => this.outletHasChild(outlet, guid)));
  }

  static outletHasChild(outlet: IViewLayoutOutlet, guid: string): boolean {
    return Boolean(outlet.columns.find((column) => column.children.includes(guid)));
  }

  static getViewLayoutTemplate(layoutTemplate: number): IViewLayout {
    const layoutItem =
      RX_VIEW_LAYOUTS.find((item) => item.id === layoutTemplate) ||
      RX_VIEW_LAYOUTS.find((item) => item.id === RX_VIEW_DEFINITION.defaultLayoutTemplateId);

    return layoutItem.layout;
  }

  static getLayoutName(layout: IViewLayout): string {
    const checksum = getLayoutChecksum(layout);
    const layoutTemplate = RX_VIEW_LAYOUTS.find((item) => item.checksum === checksum);

    return layoutTemplate ? layoutTemplate.label : '';
  }

  static removeChildFromLayout(layout: IViewLayout, guidToRemove: string): IViewLayout {
    return {
      outlets: layout.outlets.map((outlet) => ({
        ...outlet,
        columns: outlet.columns.map((column) => ({
          ...column,
          children: column.children.filter((childGuid) => childGuid !== guidToRemove)
        }))
      }))
    };
  }
}
