import { IViewActionDescriptor, IViewActionDesignManager } from './view-action.interfaces';
import { Injectable } from '@angular/core';
import {
  IBundleDescriptor,
  RX_APPLICATION,
  RxBundleCacheService,
  RxGlobalCacheService
} from '@helix/platform/shared/api';
import { isEmpty, some } from 'lodash';
import { RxStringService } from '@helix/platform/utils';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { ViewComponentPropertyType } from '../registries/view-component-descriptor.types';

@Injectable({
  providedIn: 'root'
})
export class RxViewActionRegistryService {
  constructor(
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxStringService: RxStringService,
    private rxBundleCacheService: RxBundleCacheService
  ) {}

  private viewActionDescriptors: Map<string, IViewActionDescriptor> = new Map();
  private designManagers: Map<string, IViewActionDesignManager> = new Map();

  private ownerBundleIds$ = this.rxGlobalCacheService.getBundleDescriptors().pipe(
    map((bundleDescriptors) =>
      bundleDescriptors.reduce((ownerBundleIds, bundleDescriptor) => {
        bundleDescriptor.uiOptions.viewActions?.forEach((viewActionName: string) => {
          ownerBundleIds[viewActionName] = bundleDescriptor.id;
        });

        return ownerBundleIds;
      }, {})
    ),
    shareReplay(1)
  );

  register(viewActionDescriptor: IViewActionDescriptor) {
    if (!viewActionDescriptor.label) {
      viewActionDescriptor.label = viewActionDescriptor.name;
    }

    viewActionDescriptor.parameters = viewActionDescriptor.parameters || [];

    viewActionDescriptor.parameters.push({
      name: '$condition$',
      enableExpressionEvaluation: true
    });

    viewActionDescriptor.parameters.push({
      name: 'index',
      designType: ViewComponentPropertyType.Number
    });

    this.viewActionDescriptors.set(viewActionDescriptor.name, viewActionDescriptor);
  }

  get(actionName: string): IViewActionDescriptor {
    return this.viewActionDescriptors.get(actionName);
  }

  getRegisteredActions(): Map<string, IViewActionDescriptor> {
    return this.viewActionDescriptors;
  }

  getLicensedActions(): Observable<IViewActionDescriptor[]> {
    return this.rxGlobalCacheService.getLicensedBundleDescriptors().pipe(
      map((bundleDescriptors) => {
        return Array.from(this.viewActionDescriptors.values()).filter((actionDescriptor) => {
          return (
            this.isBundleLicensed(actionDescriptor.bundleId, bundleDescriptors) &&
            this.isActionAvailableInCurrentBundle(actionDescriptor)
          );
        });
      })
    );
  }

  getActionOwnerBundleId(viewActionName: string): Observable<string> {
    return this.ownerBundleIds$.pipe(map((ownerBundleIds) => ownerBundleIds[viewActionName]));
  }

  private isBundleLicensed(bundleId: string, bundleDescriptors: IBundleDescriptor[]): boolean {
    return bundleId === RX_APPLICATION.platformBundleId || some(bundleDescriptors, { id: bundleId });
  }

  private isActionAvailableInCurrentBundle(actionDescriptor: IViewActionDescriptor): boolean {
    return (
      isEmpty(actionDescriptor.availableInBundles) ||
      this.rxStringService.isIncluded(this.rxBundleCacheService.bundleId, actionDescriptor.availableInBundles)
    );
  }

  registerUnknownAction(unknownActionName: string): IViewActionDescriptor {
    let unknownActionDescriptor = this.get(unknownActionName);

    if (!unknownActionDescriptor) {
      unknownActionDescriptor = {
        ...this.get('rxUnknownViewAction'),
        name: unknownActionName
      };

      this.register(unknownActionDescriptor);
    }

    return unknownActionDescriptor;
  }

  registerDesignManager(actionName: string, designManagerService: IViewActionDesignManager): void {
    this.designManagers.set(actionName, designManagerService);
  }

  getDesignManager(actionName: string): IViewActionDesignManager {
    return this.designManagers.get(actionName) ?? this.viewActionDescriptors.get(actionName)?.designManager;
  }
}
