import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import {
  IBundleDescriptor,
  RX_APPLICATION,
  RxComponentCanDeactivateGuard,
  RxGlobalCacheService,
  RxLocalizationService,
  RxSessionService
} from '@helix/platform/shared/api';
import { find, findIndex, get, head } from 'lodash';
import { combineLatest, Observable, of } from 'rxjs';
import { concatMap, map, switchMap, tap } from 'rxjs/operators';
import { RX_SHELL, RxShellService } from '../shell';
import { RxTreeService } from '@helix/platform/utils';

@Injectable({
  providedIn: 'root'
})
export class RxHomepageResolver implements CanActivate {
  private unknownApplicationRoute = '/unknown-application';

  constructor(
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxShellService: RxShellService,
    private rxTreeService: RxTreeService,
    private router: Router,
    private rxSessionService: RxSessionService,
    private rxComponentCanDeactivateGuard: RxComponentCanDeactivateGuard,
    private rxLocalizationService: RxLocalizationService
  ) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    const bundleId = route.paramMap.get('bundleId');
    const applicationId = head(route.url).path;
    const useDefaultLang = applicationId === RX_APPLICATION.innovationStudioBundleId;
    const initTranslations$ = this.rxLocalizationService.initTranslations(useDefaultLang);

    // waiting for translations to resolve to make getShellConfig call with correct lang
    return combineLatest([this.rxSessionService.sessionActive$, initTranslations$]).pipe(
      switchMap(() => {
        return this.rxGlobalCacheService
          .getBundleDescriptors()
          .pipe(concatMap((bundleDescriptors) => this.getBundleUrl(bundleDescriptors, bundleId)));
      }),
      tap(() => this.rxComponentCanDeactivateGuard.disable())
    );
  }

  private getBundleUrl(bundleDescriptors: IBundleDescriptor[], bundleId: string): Observable<UrlTree | boolean> {
    if (findIndex(bundleDescriptors, { id: bundleId }) !== -1) {
      return this.rxShellService.getShellConfig(bundleId).pipe(
        map((shellConfiguration) => {
          const menuItems = get(
            this.rxTreeService.flattenTree(shellConfiguration, 'navigationBarItems'),
            '[0].flattenedMenuItems'
          );

          const defaultView = find(menuItems, {
            type: RX_SHELL.actions.navigateToView
          });

          return this.generateUrl(defaultView, bundleId);
        })
      );
    } else {
      return of(this.router.parseUrl(this.unknownApplicationRoute));
    }
  }

  private generateUrl(defaultView, bundleId): UrlTree {
    return this.router.parseUrl(
      defaultView
        ? `/${bundleId}/view/${defaultView.viewUrl}`
        : `/${bundleId}/view/${RX_APPLICATION.settingsBundleId}:Unknown Default View Error)}`
    );
  }
}
