import { BinaryExpression, CallExpression, Expression, Identifier, Literal, UnaryExpression } from 'jsep';
import { every, find, forEach, includes, isNaN, isNil, isString, isUndefined, result, trim } from 'lodash';
import { RxExpressionHelperService } from './expression-helper.service';
import { RxExpressionSyntaxTreeBuilderService } from './expression-syntax-tree-builder.service';
import { IExpressionEvaluator } from './expression-evaluator.types';
import { Injectable } from '@angular/core';
import { RxStringService } from '@helix/platform/utils';
import BigNumber from 'bignumber.js';
import { ExpressionOperator } from '@helix/platform/shared/api';
import { RX_EXPRESSION_FUNCTIONS, RX_SUPPORTED_FUNCTION } from './expression-functions.constant';

@Injectable({
  providedIn: 'root'
})
export class RxDefaultExpressionEvaluatorService implements IExpressionEvaluator {
  private parsedExpressionCache = {};

  private operators = {
    [ExpressionOperator.Equal]: (a, b) => {
      if (BigNumber.isBigNumber(a)) {
        return a.isEqualTo(b);
      } else if (BigNumber.isBigNumber(b)) {
        return b.isEqualTo(a);
      } else {
        // tslint:disable-next-line:triple-equals
        return a == b;
      }
    },
    [ExpressionOperator.Add]: (a, b) => {
      if ((isNil(a) || isNaN(a)) && (isNil(b) || isNaN(b))) {
        return null;
      } else if ((isNil(a) || isNaN(a)) && !(isNil(b) || isNaN(b))) {
        return BigNumber.isBigNumber(b) ? Number(b) : b;
      } else if ((isNil(b) || isNaN(b)) && !(isNil(a) || isNaN(a))) {
        return BigNumber.isBigNumber(a) ? Number(a) : a;
      }

      // Have to convert BigNumber values to Number in order to use mathimatical addition as opposed to
      // string concatenation, which will otherwise happen due to BigNumber.valueOf() returning a string.
      a = BigNumber.isBigNumber(a) ? Number(a) : a;
      b = BigNumber.isBigNumber(b) ? Number(b) : b;

      return a + b;
    },
    [ExpressionOperator.Subtract]: (a, b) => {
      if (isUndefined(a) || isNaN(a)) {
        a = 0;
      }

      if (isUndefined(b) || isNaN(b)) {
        b = 0;
      }

      return a - b;
    },
    [ExpressionOperator.Multiply]: (a, b) => {
      if (isUndefined(a) || isNaN(a)) {
        a = 0;
      }

      if (isUndefined(b) || isNaN(b)) {
        b = 0;
      }

      return a * b;
    },
    [ExpressionOperator.Divide]: (a, b) => {
      if (isUndefined(a) || isUndefined(b) || isNaN(a) || isNaN(b)) {
        return 0;
      }

      return a / b;
    },
    [ExpressionOperator.LessThan]: (a, b) => {
      if (BigNumber.isBigNumber(a)) {
        return a.lt(b);
      } else if (BigNumber.isBigNumber(b)) {
        return b.gt(a);
      } else {
        return a < b;
      }
    },
    [ExpressionOperator.GreaterThan]: (a, b) => {
      if (BigNumber.isBigNumber(a)) {
        return a.gt(b);
      } else if (BigNumber.isBigNumber(b)) {
        return b.lt(a);
      } else {
        return a > b;
      }
    },
    [ExpressionOperator.GreaterThanOrEqual]: (a, b) => {
      if (BigNumber.isBigNumber(a)) {
        return a.gte(b);
      } else if (BigNumber.isBigNumber(b)) {
        return b.lte(a);
      } else {
        return a >= b;
      }
    },
    [ExpressionOperator.LessThanOrEqual]: (a, b) => {
      if (BigNumber.isBigNumber(a)) {
        return a.lte(b);
      } else if (BigNumber.isBigNumber(b)) {
        return b.gte(a);
      } else {
        return a <= b;
      }
    },
    [ExpressionOperator.NotEqual]: (a, b) => {
      if (BigNumber.isBigNumber(a)) {
        return !a.isEqualTo(b);
      } else if (BigNumber.isBigNumber(b)) {
        return !b.isEqualTo(a);
      } else {
        // tslint:disable-next-line:triple-equals
        return a != b;
      }
    },
    [ExpressionOperator.Remainder]: (a, b) => {
      if (isUndefined(a) || isUndefined(b) || isNaN(a) || isNaN(b)) {
        return 0;
      }

      return a % b;
    },
    [ExpressionOperator.And]: this.andHandler,
    [ExpressionOperator.And.toLowerCase()]: this.andHandler,
    [ExpressionOperator.Or]: this.orHandler,
    [ExpressionOperator.Or.toLowerCase()]: this.orHandler,
    [ExpressionOperator.Like]: this.likeHandler,
    [ExpressionOperator.Like.toLowerCase()]: this.likeHandler,
    [ExpressionOperator.Contains]: this.likeHandler,
    [ExpressionOperator.Contains.toLowerCase()]: this.likeHandler
  };

  private unaryOperators = {
    '-': (value) => {
      if (isUndefined(value) || isNaN(value)) {
        return 0;
      } else {
        return -Number(value);
      }
    },
    '!': (value) => !Boolean(value)
  };

  constructor(
    private rxExpressionHelperService: RxExpressionHelperService,
    private rxStringService: RxStringService,
    private rxExpressionSyntaxTreeBuilderService: RxExpressionSyntaxTreeBuilderService
  ) {}

  parseExpression(expression: string): Expression {
    if (!this.parsedExpressionCache[expression]) {
      const parsedExpression = this.rxExpressionSyntaxTreeBuilderService.buildTree(expression);

      if (this.validateChildNodeTypes(parsedExpression)) {
        this.parsedExpressionCache[expression] = parsedExpression;
      } else {
        throw new Error('Invalid syntax');
      }
    }

    return this.parsedExpressionCache[expression];
  }

  evaluate(expression: string, data: object) {
    let evaluatedExpression = expression;

    expression = trim(expression);

    if (this.rxStringService.isNonEmptyString(expression)) {
      const preparedExpression = this.rxExpressionHelperService.prepare(expression);
      let parsedExpression;

      try {
        parsedExpression = this.parseExpression(preparedExpression);
      } catch (e) {
        throw new Error(`Cannot parse expression "${expression}": ${e.message}.`);
      }

      try {
        evaluatedExpression = this.evaluateNode(parsedExpression, data || {});
      } catch (e) {
        throw new Error(`Cannot evaluate expression "${expression}": ${e.message}.`);
      }
    } else {
      evaluatedExpression = null;
    }

    return evaluatedExpression;
  }

  private andHandler(a, b): boolean {
    return Boolean(a) && Boolean(b);
  }

  private orHandler(a, b): boolean {
    return Boolean(a) || Boolean(b);
  }

  private likeHandler(a, b): boolean {
    return includes(a, b);
  }

  private validateChildNodeTypes(node: Expression): boolean {
    let isValid = false;

    switch (node.type) {
      case 'BinaryExpression':
        isValid =
          this.validateChildNodeTypes((node as BinaryExpression).left) &&
          this.validateChildNodeTypes((node as BinaryExpression).right);
        break;

      case 'UnaryExpression':
        isValid = this.validateChildNodeTypes((node as UnaryExpression).argument);
        break;

      case 'CallExpression':
        isValid = false;

        const functionNode = node as CallExpression;
        const functionDescriptor = find(RX_EXPRESSION_FUNCTIONS, { name: functionNode.callee['name'] });

        if (functionDescriptor && functionNode.arguments.length === functionDescriptor.parameters.length) {
          isValid = every(functionNode.arguments, (argument) => this.validateChildNodeTypes(argument));
        }

        break;

      case 'Literal':
        isValid = true;
        break;

      case 'Identifier':
        isValid = /^\$[A-Za-z]+\$$/.test((node as Identifier).name);
        break;
    }

    return isValid;
  }

  private evaluateNode(node: Expression, data: object) {
    switch (node.type) {
      case 'BinaryExpression':
        const left = this.evaluateNode((node as BinaryExpression).left, data);
        const right = this.evaluateNode((node as BinaryExpression).right, data);

        if (this.operators[(node as BinaryExpression).operator]) {
          const binaryResult = this.operators[(node as BinaryExpression).operator](left, right);

          if (isNaN(binaryResult)) {
            throw new Error(
              `Operator: ${(node as BinaryExpression).operator} has invalid arguments: ${left}, ${right}`
            );
          } else {
            return binaryResult;
          }
        } else {
          throw new Error(`Unknown binary operator: ${(node as BinaryExpression).operator}`);
        }

      case 'CallExpression':
        let evaluatedArguments = [];
        const functionName = (node as CallExpression).callee['name'];

        evaluatedArguments = (node as CallExpression).arguments.map((argument) => this.evaluateNode(argument, data));

        if (RX_SUPPORTED_FUNCTION[functionName]) {
          return RX_SUPPORTED_FUNCTION[functionName](...evaluatedArguments);
        } else {
          throw new Error(`Unknown function: ${functionName}`);
        }

      case 'Literal':
        let nodeValue = (node as Literal).value;

        if (isString(nodeValue as string)) {
          if (/^rx-\${[^{}$]+}$/.test(nodeValue as string)) {
            nodeValue = result(data, (nodeValue as string).slice(5, -1));
          } else {
            const expressions = (nodeValue as string).match(/(rx-\${[^{}$]+})/g);

            forEach(expressions, (expression) => {
              const value = result<any>(data, expression.slice(5, -1));

              nodeValue = (nodeValue as string).replace(expression, isNil(value) ? '' : value);
            });
          }
        }

        return nodeValue;

      case 'UnaryExpression':
        if (this.unaryOperators[(node as UnaryExpression).operator]) {
          const evaluatedResult = this.evaluateNode((node as UnaryExpression).argument, data);
          const unaryResult = this.unaryOperators[(node as UnaryExpression).operator](evaluatedResult);

          if (isNaN(unaryResult)) {
            throw new Error(`Operator: ${(node as UnaryExpression).operator} has invalid argument: ${evaluatedResult}`);
          } else {
            return unaryResult;
          }
        } else {
          throw new Error(`Unknown unary operator: ${(node as UnaryExpression).operator}`);
        }

      default:
        throw new Error('Invalid syntax');
    }
  }
}
