import { ComponentFactoryResolver, Injectable } from '@angular/core';
import {
  IBundleDescriptor,
  RX_APPLICATION,
  RxBundleCacheService,
  RxGlobalCacheService,
  RxLogService
} from '@helix/platform/shared/api';
import { RxStringService } from '@helix/platform/utils';
import { isEmpty, some } from 'lodash';
import { defer, from, Observable, of } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';
import { IViewComponentDescriptor } from './view-component-descriptor.types';

@Injectable({
  providedIn: 'root'
})
export class RxViewComponentRegistryService {
  // contains all registered component descriptors
  private componentDescriptors: Map<string, IViewComponentDescriptor> = new Map();
  private asyncComponentDescriptors: Promise<IViewComponentDescriptor>[] = [];

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxBundleCacheService: RxBundleCacheService,
    private rxStringService: RxStringService,
    private rxLogService: RxLogService
  ) {}

  private ownerBundleIds$ = this.rxGlobalCacheService.getBundleDescriptors().pipe(
    map((bundleDescriptors) =>
      bundleDescriptors.reduce((ownerBundleIds, bundleDescriptor) => {
        bundleDescriptor.uiOptions.viewComponents?.forEach((viewComponentName: string) => {
          ownerBundleIds[viewComponentName] = bundleDescriptor.id;
        });

        return ownerBundleIds;
      }, {})
    ),
    shareReplay(1)
  );

  resolveAsyncDescriptors(): Observable<Map<string, IViewComponentDescriptor>> {
    if (this.asyncComponentDescriptors.length) {
      this.rxLogService.debug(`Resolving ${this.asyncComponentDescriptors.length} view component descriptor(s)...`);

      return defer(() =>
        from(
          Promise.all(this.asyncComponentDescriptors).then((descriptors) => {
            descriptors.forEach((descriptor) => {
              this.registerSync(descriptor);
            });

            this.rxLogService.debug(
              `Resolved ${descriptors.length} view component descriptors: ${descriptors
                .map((desc) => desc.type)
                .join(', ')}.`
            );

            return this.componentDescriptors;
          })
        )
      );
    } else {
      return of(this.componentDescriptors);
    }
  }

  getRegisteredComponents(): Map<string, IViewComponentDescriptor> {
    return this.componentDescriptors;
  }

  get(type: string): IViewComponentDescriptor {
    return this.componentDescriptors.get(type);
  }

  register(...componentDescriptors: IViewComponentDescriptor[] | Promise<IViewComponentDescriptor>[]): void {
    componentDescriptors.forEach((descriptor) => {
      this.asyncComponentDescriptors.push(Promise.resolve(descriptor));
    });
  }

  registerSync(descriptor: IViewComponentDescriptor): void {
    if (descriptor.aliases?.includes(descriptor.type)) {
      this.rxLogService.warning(
        `Component ${descriptor.type} should not have its type listed in descriptor's aliases property.`
      );

      descriptor.aliases = descriptor.aliases.filter((type) => type !== descriptor.type);
    }

    this.componentDescriptors.set(descriptor.type, descriptor);

    descriptor.aliases?.forEach((type) => {
      this.componentDescriptors.set(type, descriptor);
    });
  }

  isDataComponentDescriptor(componentDescriptor: IViewComponentDescriptor): boolean {
    return Boolean(componentDescriptor.isDataComponent);
  }

  getBundlePageComponents(bundleId: string, includeAliases = false): IViewComponentDescriptor[] {
    return this.getDescriptors(includeAliases).filter((componentDescriptor: IViewComponentDescriptor) =>
      componentDescriptor.isPageComponent && isEmpty(componentDescriptor.availableInBundles)
        ? componentDescriptor.bundleId === bundleId
        : this.rxStringService.isIncluded(bundleId, componentDescriptor.availableInBundles)
    ) as IViewComponentDescriptor[];
  }

  getLicensedComponents(includeAliases = false): Observable<IViewComponentDescriptor[]> {
    return this.rxGlobalCacheService
      .getLicensedBundleDescriptors()
      .pipe(
        map((bundleDescriptors) =>
          this.getDescriptors(includeAliases).filter(
            (componentDescriptor) =>
              this.isBundleLicensed(componentDescriptor.bundleId, bundleDescriptors) &&
              this.isComponentAvailableInCurrentBundle(componentDescriptor)
          )
        )
      );
  }

  getComponentOwnerBundleId(viewComponentType: string): Observable<string> {
    return this.ownerBundleIds$.pipe(map((ownerBundleIds) => ownerBundleIds[viewComponentType]));
  }

  private isBundleLicensed(bundleId: string, bundleDescriptors: IBundleDescriptor[]): boolean {
    return bundleId === RX_APPLICATION.platformBundleId || some(bundleDescriptors, { id: bundleId });
  }

  private isComponentAvailableInCurrentBundle(componentDescriptor: IViewComponentDescriptor): boolean {
    return (
      isEmpty(componentDescriptor.availableInBundles) ||
      this.rxStringService.isIncluded(this.rxBundleCacheService.bundleId, componentDescriptor.availableInBundles)
    );
  }

  private getDescriptors(includeAliases = false): IViewComponentDescriptor[] {
    return [...this.componentDescriptors.entries()].reduce((result, [type, descriptor]) => {
      if (includeAliases || !descriptor.aliases?.includes(type)) {
        result.push(descriptor);
      }

      return result;
    }, []);
  }
}
