import { ComponentFactory, Injector, Type } from '@angular/core';
import { IExpressionConfigurator, IExpressionConfiguratorProvider, IPlainObject } from '@helix/platform/shared/api';
import { IExpressionEvaluator } from '../expressions/expression-evaluator.types';
import { IViewDefinitionPermission } from '../domain/view-definition-permission.interface';

export interface IViewComponentDescriptor {
  // type/selector that uniquely identifies the component
  type: string;

  // alternative types that can be used by component
  aliases?: string[];

  // named areas withing a component where the child components will be rendered
  outlets?: { name: string }[];

  // component factory for creating the component at runtime
  componentFactory?: ComponentFactory<any>; // IViewComponent

  // descriptors of a component's runtime properties
  properties?: IComponentPropertyDescriptor[];

  // name of a property in the parent component's propertiesByName where
  // propertiesByName of the current data component will be stored at runtime
  configPropertyName?: string;

  // name of the component as displayed in the view designer palette
  // and used as the default label
  name?: string;

  // optional index that used for sorting components in palette
  index?: number;

  // ID of a bundle to which this component logically belongs.
  // If the specified bundle is not deployed, the component will not be available
  // in the view designer palette.
  bundleId?: string;

  // list of bundle IDs within which this component will be available at design time;
  // if not specified, or blank, the component will be available in all bundles.
  availableInBundles?: Array<string | RegExp>;

  // component factory for creating the component at design time
  designComponentFactory?: ComponentFactory<any>;

  // component's design time model
  designComponentModel?: IViewDesignerComponentModelConstructor;

  expressionConfigurator?: Type<IExpressionConfigurator>;

  // name of a view designer palette group in which this component will be displayed
  group?: string;

  // flag indicating whether the component should be hidden in the view designer palette;
  // data and page components are always hidden
  hidden?: boolean;

  // CSS class name for the icon to display in the view designer palette
  icon?: string;

  // flag indicating whether this component can contain child components, including data components
  isContainerComponent?: boolean;

  // flag indicating whether this is a data component
  // used for storing the data related to its parent component
  // but not rendered as an individual component, e.g. record grid column
  isDataComponent?: boolean;

  // flag indicating whether this is a page component
  isPageComponent?: boolean;

  // additional options
  options?: IPlainObject;

  // callback that is executed to decide whether this component can be dropped
  // on a target component on the view designer canvas
  canBeInsertedInto?(componentTypes: string[]): boolean;
}

export interface IViewDesignerComponentModel<TProperties = IPlainObject, TDesignProperties = TProperties>
  extends IExpressionConfiguratorProvider {
  rxInit?(): void;

  getPropertiesByName?(props: TDesignProperties): TProperties;
}

export interface IViewDesignerComponentModelConstructor {
  new (injector: Injector, sandbox: any): IViewDesignerComponentModel;

  getInitialProperties?(initialProperties: IPlainObject): IPlainObject;

  getDefaultPermissions?(): IViewDefinitionPermission[];
}

export interface IComponentPropertyDescriptor {
  name: string;
  label?: string;
  // if provided, runtime will try to cast property value to provided type
  type?: ViewComponentPropertyType;
  // if provided, designer will try to cast property value to provided type
  designType?: ViewComponentPropertyType;
  enableExpressionEvaluation?: boolean;
  // custom Expression Evaluator Service
  evaluatorService?: IExpressionEvaluator;
  // if provided true, property value will be localizable
  localizable?: boolean;
}

export enum ViewComponentPropertyType {
  Boolean = 'boolean',
  String = 'string',
  Number = 'number',
  Array = 'array',
  Object = 'object'
}
