import { Injectable } from '@angular/core';
import { IViewDefinition } from '../domain/view-definition.interface';
import { IViewComponentDefinitionWithParent } from '../domain/view-component-definition-with-parent.interface';
import {
  AnyViewComponentDefinition,
  IContainerViewComponentDefinition,
  IViewComponentDefinition
} from '../domain/view-component-definition.types';
import { find } from 'lodash';
import { RxTreeService } from '@helix/platform/utils';
import { RxPredicate } from '@helix/platform/shared/api';

@Injectable({
  providedIn: 'root'
})
export class RxViewDefinitionParserService {
  constructor(private rxTreeService: RxTreeService) {}

  getComponents(
    definition: IContainerViewComponentDefinition | IViewDefinition,
    skipInitial = false
  ): IViewComponentDefinitionWithParent[] {
    const components: IViewComponentDefinitionWithParent[] = [];

    if (!skipInitial) {
      components.push({
        componentDefinition: definition,
        parentComponentDefinition: null
      } as IViewComponentDefinitionWithParent);
    }

    this.processComponents(definition.componentDefinitions, components, definition);

    return components;
  }

  private processComponents(
    componentDefinitions: Array<IViewComponentDefinition | IContainerViewComponentDefinition>,
    components: IViewComponentDefinitionWithParent[],
    parentComponentDefinition: IContainerViewComponentDefinition | IViewDefinition
  ) {
    componentDefinitions.forEach((componentDefinition) => {
      components.push({ componentDefinition, parentComponentDefinition } as IViewComponentDefinitionWithParent);

      if (this.isContainerViewComponentDefinition(componentDefinition)) {
        this.processComponents(componentDefinition.componentDefinitions, components, componentDefinition);
      }
    });
  }

  private isContainerViewComponentDefinition(
    componentDefinition: IViewComponentDefinition | IContainerViewComponentDefinition
  ): componentDefinition is IContainerViewComponentDefinition {
    return (componentDefinition as IContainerViewComponentDefinition).componentDefinitions !== undefined;
  }

  findParentComponentDefinition(
    viewDefinition: IViewDefinition,
    childComponentDefinition: Partial<AnyViewComponentDefinition>,
    predicate: (definition: AnyViewComponentDefinition) => boolean
  ): IContainerViewComponentDefinition {
    const componentDefinitionsWithParent = this.getComponents(viewDefinition);
    let parentComponentDefinition;

    let childComponentDefinitionWithParent = componentDefinitionsWithParent.find(
      (pair) => pair.componentDefinition.guid === childComponentDefinition.guid
    );

    while (childComponentDefinitionWithParent && !parentComponentDefinition) {
      if (predicate(childComponentDefinitionWithParent.componentDefinition)) {
        parentComponentDefinition = childComponentDefinitionWithParent.componentDefinition;
      } else {
        childComponentDefinitionWithParent = componentDefinitionsWithParent.find(
          (pair) => pair.componentDefinition.guid === childComponentDefinitionWithParent.parentComponentDefinition.guid
        );
      }
    }

    return parentComponentDefinition;
  }

  findViewComponent(
    viewComponentContainer: IContainerViewComponentDefinition | IViewDefinition,
    predicate: RxPredicate
  ): IViewComponentDefinition {
    return find(this.rxTreeService.flattenTree(viewComponentContainer, 'componentDefinitions'), predicate);
  }
}
